<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_4_1
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h1 4.1.	Normativa ambiental: clasificación de residuos y cuidado del entorno
    
    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La gestión ambiental en operaciones turísticas en entornos étnicos se rige por un marco normativo integral que establece los parámetros para el desarrollo de actividades turísticas sostenibles. Este marco incluye:
      .p •	La Constitución Política de 1991, que en su artículo 79 establece el derecho a un ambiente sano y la obligación de proteger la diversidad e integridad del ambiente.
      .p •	La Ley 99 de 1993, que crea el Ministerio del Ambiente y organiza el Sistema Nacional Ambiental (SINA).
      .p •	La Ley 300 de 1996 (Ley General de Turismo), que promueve el desarrollo turístico sostenible.
      .p •	El Decreto 1076 de 2015, que compila las disposiciones del sector ambiente.
      .p •	La Resolución 2184 de 2019 del Ministerio de Ambiente y Desarrollo Sostenible (MADS), que establece el código de colores unificado para la separación de residuos en la fuente.
      .p •	El CONPES 4080 de 2022, que adopta la Política de Turismo Sostenible en Colombia.
          
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p El principio de reciprocidad con la naturaleza es un concepto central en las cosmovisiones étnicas, que reconoce la interdependencia mutua entre los seres humanos y el entorno natural. Este principio parte de la comprensión de que los recursos naturales no son mercancías, sino dones que requieren correspondencia y retribución por parte de las comunidades. A continuación, se explican las características del principio de reciprocidad:

    br
    .pb-3(data-aos="fade-left")
      .container.tarjeta.tarjeta--blanca.p-4.p-md-5
        .row.mb-8.justify-content-center
          .col-md-6.col-xl.mb-4.mb-xl-0
            .tarjeta-numerada.color-primario.p-5
              .tarjeta-numerada__numero
                .h2 1
              h5 Relación bidireccional: 
              p la naturaleza provee recursos y las comunidades retribuyen mediante prácticas de conservación


          .col-md-6.col-xl.mb-4.mb-xl-0
            .tarjeta-numerada.color-secundario.p-5
              .tarjeta-numerada__numero
                .h2 2
              h5 Equilibrio dinámico: 
              p toda extracción o uso debe compensarse con acciones de preservación.
              br
              br

          .col-md-6.col-xl.mb-4.mb-xl-0
            .tarjeta-numerada.color-acento-contenido.p-5
              .tarjeta-numerada__numero
                .h2 3
              h5 Responsabilidad intergeneracional: 
              p las decisiones presentes deben garantizar la disponibilidad para futuras generaciones
              br

          .col-md-6.col-xl.mb-4.mb-xl-0
            .tarjeta-numerada.color-acento-botones.p-5
              .tarjeta-numerada__numero
                .h2 4
              h5 Respeto a los ciclos naturales: 
              p las actividades humanas deben adaptarse a los ritmos de la naturaleza.
              br
              br

    row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La implementación práctica de este marco exige que el guía turístico porte un kit completo de tres bolsas con los colores reglamentarios:

    .row.justify-content-center(data-aos="fade-left")
      .col-lg-8
        figure
          img(src='@/assets/curso/temas/tema4/tema1/rec48-img.svg', alt='Texto que describa la imagen')
              
    br
    row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p Durante la interpretación turística, el guía debe explicar técnicamente al grupo los criterios de separación en la fuente, incluyendo:
    
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      .col-md-11
        .p •	Las razones ambientales sustentadas en la normativa vigente.
        .p •	Las fundamentaciones culturales derivadas del principio de reciprocidad.
        .p •	Los impactos específicos en el territorio comunitario.
        p •	Los procedimientos de disposición final autorizados por la autoridad ambiental.
      p Más allá del cumplimiento normativo, la interpretación patrimonial debe incorporar el principio de “Cero Huella”, adaptado a la cosmovisión cultural local, que comprende:

    .row.justify-content-center
      .col-lg-12
        .crd.tarjeta.acordeonA
          PasosB.color-acento-botones.p-4(data-aos="fade-right")
            .row.justify-content-center(titulo="").pt-1
              .col-lg-7.mb-4.mb-lg-0
                br
                p Gestión integral de residuos
                ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Separación en la fuente según el código de colores unificado.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Compostaje de residuos orgánicos en áreas autorizadas.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Almacenamiento y disposición final adecuada de residuos no reciclables.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Reutilización creativa de materiales según prácticas locales.

              .col-lg-3.col-6
                figure
                  img(src='@/assets/curso/temas/tema4/tema1/rec47-img1.svg', alt='')

            .row.justify-content-center(titulo="").pt-1
              .col-lg-7.mb-4.mb-lg-0
                br
                p Minimización del impacto acústico
                ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Control de niveles de sonido durante las actividades.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Respeto por los espacios de silencio ceremonial.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Uso racional de equipos de amplificación solo cuando sea estrictamente necesario.
                 
              .col-lg-3.col-6
                figure
                  img(src='@/assets/curso/temas/tema4/tema1/rec47-img2.svg', alt='')
            
            .row.justify-content-center(titulo="").pt-1
              .col-lg-7.mb-4.mb-lg-0
                br
                p Restricción al uso de plásticos de un solo uso
                ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Sustitución por materiales biodegradables o tradicionales.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Implementación de sistemas reutilizables.
                  
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Promoción del consumo responsable de agua y alimentos

              .col-lg-3.col-6
                figure
                  img(src='@/assets/curso/temas/tema4/tema1/rec47-img3.svg', alt='')
            
            .row.justify-content-center(titulo="").pt-1
              .col-lg-7.mb-4.mb-lg-0
                br
                p Protección de fuentes hídricas
                ol.lista-ol--cuadro
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Zonas de amortiguamiento en torno a cuerpos de agua.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Prohibición de uso de químicos en actividades turísticas.
                  li 
                    .lista-ol--cuadro__vineta.clr-color-adicional-4
                      span
                    span Control de vertimientos y manejo de afluentes.

              .col-lg-3.col-6
                figure
                  img(src='@/assets/curso/temas/tema4/tema1/rec47-img4.svg', alt='')


    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      h3 Coordinación con sistemas comunitarios
      p Estas prácticas deben coordinarse con los sistemas de manejo ambiental propios de las comunidades, respetando su autonomía en la gestión territorial. Esta coordinación implica:
      .col-md-11
        .p •	Reconocimiento de las autoridades ambientales tradicionales.
        .p •	Armonización con los calendarios ecológicos tradicionales.
        .p •	Integración de conocimientos ancestrales sobre manejo de ecosistemas.
        .p •	Respeto por las áreas de protección cultural y espiritual.
    
    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p A continuación se expone un ejemplo técnico-normativo ampliado y aplicado:

    .row.justify-content-center
      .col-lg-9
        ImagenInfograficaB.color-primario
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema4/tema1/rec46-modal-bg.svg', alt='Texto que describa la imagen')
              
          div(x="26.5%" y="52%" tooltip="Ciclos de la comunidad" numero="")
            figure
              img(src='@/assets/curso/temas/tema4/tema1/rec46-img1.svg', alt='imagen decorativa')
                
          div(x="50.9%" y="52%" tooltip="Transparencia: disponibilidad y precios", numero="")
            figure
              img(src='@/assets/curso/temas/tema4/tema1/rec46-img2.svg', alt='imagen decorativa')
                
          div(x="75.8%" y="52%" tooltip="Flexibilidad y adaptabilidad", numero="")
            figure
              img(src='@/assets/curso/temas/tema4/tema1/rec46-img3.svg', alt='imagen decorativa')
            

